import React from "react";
import * as FileSaver from "file-saver";
import * as XLSX from "xlsx";

interface ExportToExcelProps {
  fileData: any[];
  fileName: string;
  btnName?: string;
  className?: string;
}

const ExportToExcel: React.FC<ExportToExcelProps> = ({
  fileData,
  fileName,
  btnName = "Excel",
  className = "px-4 py-1 ml-2 rounded bg-white border hover:text-blue-600 hover:bg-blue-50 cursor-pointer border-gray-200 font-bold",
}) => {
  const fileType =
    "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8";
  const fileExtension = ".xlsx";

  const exportToCSV = (apiData: any[], fileName: string) => {
    const ws = XLSX.utils.json_to_sheet(apiData);
    const wb = { Sheets: { data: ws }, SheetNames: ["data"] };
    const excelBuffer = XLSX.write(wb, { bookType: "xlsx", type: "array" });
    const data = new Blob([excelBuffer], { type: fileType });
    FileSaver.saveAs(data, fileName + fileExtension);
  };

  return (
    <button
      onClick={(e) => exportToCSV(fileData, fileName)}
      className={className}
    >
      {btnName}
    </button>
  );
};

export default ExportToExcel;
